import {InferActionsType} from "./redux-store";


export type noteType = {
    id: number,
    message: string
}

let initialState = {
    backgroundColor: 'lightpink' as string,
    note: [
        {id: 1, message: 'Hi!'},
    ] as Array<noteType>,
    status: '' as string,
    button: true as boolean
}

export type initialStateType = typeof initialState
type ActionsType = InferActionsType<typeof actions>

const settingsReducer = (state = initialState, action: ActionsType): initialStateType => { //dispatch
    switch (action.type){
        case "CHANGE_COLOR": {
            state.backgroundColor = action.backgroundColor
            document.body.style.backgroundColor = state.backgroundColor
            return {...state, backgroundColor: action.backgroundColor,}
        }
        case "NOTE_UPDATE": {
            return {...state, status: action.text}
        }
        case "ADD_NOTE": {
            let newNote = state.status
            state.status = ''
            return {...state, note: [ ...state.note, { id: 2, message: newNote } ]}
        }
        default:
            return state
    }
}

export const actions = {
    changeColor: (backgroundColor: string) => ({ type: 'CHANGE_COLOR', backgroundColor } as const), // action creator
    updateNote: (text: string) => ({ type: 'NOTE_UPDATE', text } as const),
    addNote: () => ({ type: 'ADD_NOTE' } as const),
}


export default settingsReducer;



