import React, {useEffect} from "react";

type resDataType = {
    login: string
    id: number
    avatar_url: string
}

type propsType = {
    selectUser: resDataType | null
}


export const Body: React.FC<propsType> = ({selectUser}) => {

    useEffect(() => {
        if (!!selectUser) {
            document.title = selectUser.login
        }
    }, [selectUser])

    return <div>
        <span>USER NAME</span>
        {selectUser && <span>
                    <img src={selectUser.avatar_url}/>
                    <h3>User Name: {selectUser.login}</h3> <h4>ID: {selectUser.id}</h4>
                </span>
        }
    </div>
}