import {FormAction, stopSubmit} from "redux-form";
import {PhotosType, postType, profileType} from "../types/types";
import {BaseThunkType, InferActionsType} from "./redux-store";
import {profileAPI} from "../api/profileAPI";



let initialState = {  // пости
    posts: [
        {id: 1, message: 'Hi!', likeCount: 23 },
        {id: 2, message: "You like this?", likeCount: 31 },
        {id: 3, message: "I'm fine!", likeCount: 10 },
        {id: 4, message: 'Are you?!', likeCount: 29 }
    ] as Array<postType>,
    profile: [] as profileType | Array<null>,
    status: ""
}

export type initialStateType = typeof initialState
type ActionsType = InferActionsType<typeof actions>

const profileReducer = (state = initialState, action: ActionsType): initialStateType => {
    switch (action.type){
        case "ADD_POST" : {
            let newPost = {
                id: 5, message: action.newPostText, likeCount: 0
            }
            return {
                ...state,
                posts: [ ...state.posts, newPost]
            };
        }
        case "SET_USER_PROFILE": {
            return {...state, profile: action.profile}
        }
        case "SET_STATUS": {
            return {...state, status: action.status}
        }
        case "DELETE_POST": {
            return {...state, posts: state.posts.filter(u => u.id != action.postId)}
        }
        case "SAVE_PHOTO_SUCCESS": {
            return  { ...state, profile: {...state.profile, photos: action.photos} }
        }
        default:
            return state
    }

}

export const actions = {
    addPostKey: (newPostText: string) => ({ type: 'ADD_POST', newPostText } as const),
    setUserProfile: (profile: profileType) => ({ type: 'SET_USER_PROFILE', profile } as const),
    setStatus: (status: string) => ({ type: 'SET_STATUS', status } as const),
    deletePost: (postId: number) => ({ type: 'DELETE_POST', postId } as const),
    savePhotoSuccess: (photos: PhotosType ) => ({ type: 'SAVE_PHOTO_SUCCESS', photos } as const)
}


export type ThunkType = BaseThunkType<ActionsType | FormAction>

export const getUserProfileHeader = (userId: number | null): ThunkType => async (dispatch) => {
    let response = await profileAPI.getUserProfile(userId)
    dispatch(actions.setUserProfile(response))
}

export const getStatus = (userId: number | null): ThunkType => async (dispatch) => {
    let response = await profileAPI.getUsersStatus(userId)
    dispatch(actions.setStatus(response))
}

export const updateStatus = (status: string): ThunkType => async (dispatch) => {
    let response = await profileAPI.updateStatus(status)
    if (response.resultCode === 0) {
        dispatch(actions.setStatus(status))
    }
}

export const savePhoto = (file: File): ThunkType => async (dispatch) => {
    let data = await profileAPI.savePhoto(file)
    if (data.resultCode === 0) {
        dispatch(actions.savePhotoSuccess(data.data.photos))
    }
}

export const saveProfile = (profile: profileType): ThunkType =>
    async (dispatch, getState) => {
    const userId = getState().auth.userId
    let response = await profileAPI.saveProfile(profile)
    if (response.resultCode === 0) {
        dispatch(getUserProfileHeader(userId as number))
    } else {
        let message = response.messages.length > 0 ? response.messages[0] : 'Some error'
        dispatch(stopSubmit('profile', {_error: message}))
        return Promise.reject(response.data)
    }
}

export default profileReducer;


