import React from "react";

type resDataType = {
    login: string
    id: number
    avatar_url: string
}

type propsType = {
    fetchUser: resDataType[] | null
    setSelectUser: (items: resDataType) => void
}

export const Footer: React.FC<propsType> = ({setSelectUser, fetchUser}) => {

    return <div>
        <ul>
            {
                fetchUser?.map(u =>
                    <li key={u.id} onClick={() => {
                        setSelectUser(u)
                    }}>{u.login}</li>
                )
            }
        </ul>
    </div>
}