import React from "react";
import {Settings} from "./Settings";
import {connect} from "react-redux";
import {actions, noteType} from "../../../redax/setting-reducer";
import {AppStateType} from "../../../redax/redux-store";

type MapStateProps = {
    backgroundColor: string,
    note: Array<noteType>,
    status: string,
}
type MapDispatchProps = {
    changeColor: (value: string) => void,
    addNote: () => void,
    updateNote: (text: string) => void,
}
type Props = MapStateProps & MapDispatchProps

class SettingsContainer extends React.Component<Props> {


    render() {

        return (
            <Settings  changeColor={this.props.changeColor}
                       addNote={this.props.addNote} updateNote={this.props.updateNote}
                       note={this.props.note} status={this.props.status}
            />
        )
    }
}

let mapStateToProps = (state: AppStateType) => {
    return {
        backgroundColor: state.settings.backgroundColor,
        note: state.settings.note,
        status: state.settings.status,
    }
}

export default connect (mapStateToProps,
    { changeColor: actions.changeColor, updateNote: actions.updateNote, addNote: actions.addNote
    })(SettingsContainer)


