import React from "react";
import s from './Friends.module.css'
import {friendType} from "../../../../types/types";
import {NavLink} from "react-router-dom";

type PropsType = {
    friend: friendType
}

let Friend: React.FC<PropsType> = (props) => {
    //console.log(props.friend.name)
    return (
        <div className={s.block}>
            <div className={s.firstBlock}>
                <NavLink to={'/profile/' + props.friend.id}>
                    <img className={s.image} src={props.friend.photos.small != null
                        ? props.friend.photos.small
                        : 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSAaeVfXxyG1sNBohvr-x5NOCzM9lvcF_pTzA&usqp=CAU'}/>
                </NavLink>
            </div>
            <div className={s.text}>
                <p>{props.friend.name}</p>
                <p>ID = {props.friend.id}</p>
                {/*<span>{props.friend.status ? props.friend.status : '-- not status --'}</span>*/}
            </div>

        </div>
    )
}


export default Friend;