import React from "react";
import s from "./Dialogs.module.css"
import Dialog from "./gialog1/Dialog1";
import Message from "./gialog1/massage/Message";
import {actions} from "../../../redax/dialogs-reducer";
import {FormDialogs} from "./FormDialog/FormDialogs";
import {useDispatch, useSelector} from "react-redux";
import {AppStateType} from "../../../redax/redux-store";



export const Dialogs: React.FC = () => {

    const dialogs = useSelector((state: AppStateType) => state.messagesPage.dialogs)
    const messages = useSelector((state: AppStateType) => state.messagesPage.messages)

    const dispatch = useDispatch()

    let dialogsElements = dialogs.map(d => <Dialog name={d.name} key={d.id} id={d.id}/>)
    let messagesElements = messages.map(m => <Message message={m.message} key={m.id} />)

    const addNewMesBut = (value: string) => {
        dispatch(actions.addMesKey(value))
    }

    return (
        <div className={s.dialogs}>
            <div className={s.dialogsItem}>
                {dialogsElements}
            </div>
            <div className={s.messages}>
                {messagesElements}
                <FormDialogs addNewMes={addNewMesBut} />
            </div>
        </div>
    )
}





