import React, {useEffect, useState} from "react";
import axios from "axios";

type resDataItemType = {
    items: {
        login: string
        id: number
        avatar_url: string
    }
}

type resDataType = {
    login: string
    id: number
    avatar_url: string
}

type fetchType = {
    setFetchUser: (item: resDataType[] | null) => void
}


export const Header: React.FC<fetchType> = ({setFetchUser}) => {

    const [term, setTerm] = useState('it-kamasutra')
    const [inputVal, setInputVal] = useState('')

    console.log()

    useEffect(() => {
        if (term) {
            axios.get<resDataItemType>(`https://api.github.com/search/users?q=${term}`)
                // @ts-ignore
                .then(res => setFetchUser(res.data.items))

        }
    }, [term])

    return <div>
        <input placeholder={'search'}
               value={inputVal} onChange={(e) => setInputVal(e.currentTarget.value)}/>
        <button onClick={() => setTerm(inputVal)}>Find</button>
    </div>
}