import React, {useEffect, useState} from 'react';
import './App.css';
import 'antd/dist/antd.css';
import store, {AppStateType} from "./redax/redux-store";
import {BrowserRouter as Router, Navigate, NavLink, Route, Routes} from "react-router-dom";
import {Login} from "./components/Login/login";
import {Provider, useDispatch, useSelector} from "react-redux";
import {initializeApp} from "./redax/app-reducer";
import Prelouder from "./components/common/Prelouder";
import {getFriends} from "./redax/users-reducer";
import {UsersContainer} from "./components/Navbar/Users/UsersContainer";
import {DialogsContainer} from "./components/Navbar/Dialogs/DialogsContainer";
import {ProfileContainer1} from "./components/Profile/ProfileContainer";
import {HeaderMy} from "./components/Header/Header";
import ChatPage from "./components/Navbar/Chat/ChatPage";
import {AppstoreOutlined,} from '@ant-design/icons';
import {Divider, Layout, Menu, Switch} from 'antd';
import type {MenuProps, MenuTheme} from 'antd/es/menu';
import s from "./components/Navbar/Nav.module.css";
import Friend from "./components/Navbar/Users/Friends/Friends";
import { Never } from './NEVER/Never';
import {withSuspense} from "./HOOK/withSuspend";

type MenuItem = Required<MenuProps>['items'][number]
const {Sider, Content} = Layout;


// Ctrl + Alt + Shift + T (2)


const Music = React.lazy(() => import("./components/Navbar/Music/Music"))
const SettingsContainer = React.lazy(() => import("./components/Navbar/Settings/SettingsContainer"))
const News = React.lazy(() => import("./components/Navbar/News/News"))

const SuspendedSettings = withSuspense(SettingsContainer)

function getItem(label: React.ReactNode, key?: React.Key | null,
    icon?: React.ReactNode, children?: MenuItem[],): MenuItem {
    return {
        key,
        icon,
        children,
        label,
    } as MenuItem;
}

const items: MenuItem[] = [
    getItem('My Profile', 'sub1', <AppstoreOutlined/>, [
        getItem('', '1', <NavLink to='profile'>Profile</NavLink>),
        getItem('', '2', <NavLink to='dialogs/*'>Dialogs</NavLink>),
        getItem('', '3', <NavLink to='chat'>Chat</NavLink>),
    ]),
    getItem('', '4', <NavLink to='users'>Developers</NavLink>),
    getItem('', '5', <NavLink to='settings'>Settings</NavLink>),
    getItem('', '6', <NavLink to='never'>Never</NavLink>)
];

export const App2: React.FC = (props) => {

    const catchAllUnhandledErrors = (e: PromiseRejectionEvent) => {
        alert('Something wrong')
    }

    const [mode, setMode] = useState<'vertical' | 'inline'>('vertical');
    const [theme, setTheme] = useState<MenuTheme>('dark');

    const friends = useSelector((state: AppStateType) => state.usersPage.friendsToNavBar)
    const initialized = useSelector((state: AppStateType) => state.app.initialized)

    const dispatch = useDispatch()

    useEffect(() => {
        dispatch(getFriends(1, 5))
        dispatch(initializeApp())
        window.addEventListener('unhandledrejection', catchAllUnhandledErrors)
    }, [])

    if (!initialized) {
        return <Prelouder/>
    }

    const changeMode = (value: boolean) => {
        setMode(value ? 'inline' : 'vertical');
    };

    const changeTheme = (value: boolean) => {
        setTheme(value ? 'light' : 'dark');
    };

    return (
        <div>
            <Router>
                <Layout>
                    <Sider width='300px' theme={theme}>
                        <div>
                            <Switch onChange={changeMode}/> <span  style={{color: `${theme === 'dark' ? 'azure' : 'darkblue'}`}}>Change Mode</span>
                            <Divider type="vertical"/>
                            <Switch onChange={changeTheme}/> <span style={{color: `${theme === 'dark' ? 'azure' : 'darkblue'}`}}>Change Style</span>
                            <br/>
                            <br/>
                            <Menu
                                style={{width: 300}}
                                //defaultSelectedKeys={['1']}
                                //defaultOpenKeys={['sub1']}
                                mode={mode}
                                theme={theme}
                                items={items}>
                            </Menu>
                            <div style={{margin: '0 0 0 55px'}}>
                                <div className={s.box}>
                                    <h4 style={{color: 'azure'}}>The last 3 users of which you follow</h4>
                                    {
                                        friends.map(f => <Friend key={f.id} friend={f}  />)
                                    }
                                </div>
                            </div>
                        </div>
                    </Sider>
                    <Layout className="site-layout">
                        <HeaderMy />
                        <Content
                            className="site-layout-background"
                            style={{
                                margin: '24px 16px',
                                padding: 24,
                                minHeight: 280,
                            }}
                        >
                            <Routes>
                                <Route path="/social-network-react" element={<Navigate to={'/profile'}/>}/>
                                <Route path='/dialogs/*' element={<DialogsContainer/>}/>
                                <Route path='/profile' element={<ProfileContainer1/>}/>
                                <Route path='/profile/:userId' element={<ProfileContainer1/>}/>
                                <Route path='/login' element={<Login/>}/>
                                <Route path='/news' element={<News/>}/>
                                <Route path='/music' element={<Music/>}/>
                                <Route path='/users' element={<UsersContainer/>}/>
                                <Route path='/settings' element={<SuspendedSettings/>}/>
                                <Route path='/chat' element={<ChatPage />}/>
                                <Route path='/never' element={<Never />}/>
                                <Route path='*' element={<div className='error'>404 NOT FOUND</div>}/>
                            </Routes>

                        </Content>
                    </Layout>
                </Layout>
            </Router>
        </div>
    );
}


const SamuraiJsApp2: React.FC = () => {
    return <Provider store={store}>
        <App2/>
    </Provider>
}

export default SamuraiJsApp2