import React from 'react';
import s from './Nav.module.css'
import {NavLink, Router} from "react-router-dom";
import {AppStateType} from "../../redax/redux-store";
import {connect} from "react-redux";
import {friendType} from "../../types/types";
import Friend from "./Users/Friends/Friends";

const act = (navData: any) => navData.isActive ? s.active : s.item

type PropsType = {
    friends: Array<friendType>
}

const Nav: React.FC<PropsType> = (props) => {

    return(
        <nav className={s.nav}>
          <div>
              <NavLink to="profile" className = { act }>Profile</NavLink>
          </div>
          <div>
            <NavLink to="/dialogs/*" className = { act }>Messages</NavLink>
          </div>
          <div>
            <NavLink to="news"  className = { act }>News</NavLink>
          </div>
          <div>
            <NavLink to="music" className = { act }>Music</NavLink>
          </div>
          <div>
              <NavLink to="users" className = { act }>Users</NavLink>
          </div>
            <p></p>
          <div>
            <NavLink to="settings" className = { act }>Settings</NavLink>
          </div>
            <div>
            <NavLink to="Chat" className = { act }>Chat</NavLink>
          </div>
            <div>
                <div className={s.box}>
                    {
                        props.friends.map(f => <Friend key={f.id} friend={f}  />)
                    }
                </div>
            </div>
        </nav>
    )
}

let mapStateToProps = (state: AppStateType) => ({
    friends: state.usersPage.friendsToNavBar
})

export default connect(mapStateToProps, { })(Nav)