import React from "react";
import {Field, Form, Formik} from "formik";
import s from "./FormDialog.module.css";
import * as Yup from "yup";

type ValuesType = {
    areaDialog: string
}
type FormPropsType = {
    addNewMes: (values: string) => void
}
const DisplayingErrorMes = Yup.object().shape({
    areaDialog: Yup.string()
        .min(0, 'Too Short!')
        .max(100, 'Too Long!')
        .required('Too Short!'),
});


export const FormDialogs: React.FC<FormPropsType> = (props) => {

    let onSubmit = (values: ValuesType, {setSubmitting}: { setSubmitting: (isSubmitting: boolean) => void }) => {
        props.addNewMes(values.areaDialog)
        setSubmitting(false)
        values.areaDialog = ''
    }

    return <div>
        <Formik
            initialValues={{areaDialog: ''}}
            validationSchema={DisplayingErrorMes}
            onSubmit={onSubmit}
        >
            {({isSubmitting, errors}) => (
                <Form>
                    <div>
                        <Field as="textarea" name="areaDialog" placeholder='Enter message here'
                               className={`${(errors.areaDialog) ? s.formErrorArea : s.textarea}`}/>
                        <p></p>
                        {errors.areaDialog && <div className={s.formDialogError}>{errors.areaDialog}</div>}
                    </div>
                    <p></p>
                    <button className={s.sendBut} type="submit" disabled={isSubmitting}>
                        Send
                    </button>
                </Form>
            )}
        </Formik>
    </div>
}

