import React  from 'react';
import './App.css';
import 'antd/dist/antd.css';
import Nav from "./components/Navbar/Nav";
import store, {AppStateType} from "./redax/redux-store";
import {BrowserRouter as Router, Routes, Route} from "react-router-dom";
import {Navigate} from "react-router-dom";
import {HeaderMy} from "./components/Header/Header";
import {Login} from "./components/Login/login";
import {connect, Provider} from "react-redux";
import {initializeApp} from "./redax/app-reducer";
import Prelouder from "./components/common/Prelouder";
import {compose} from "redux";
import {getFriends} from "./redax/users-reducer";
import {UsersContainer} from "./components/Navbar/Users/UsersContainer";
import {DialogsContainer} from "./components/Navbar/Dialogs/DialogsContainer";
import {ProfileContainer1} from "./components/Profile/ProfileContainer";
import Antd from "./Antd";
import ChatPage from "./components/Navbar/Chat/ChatPage";
import SettingsContainer from "./components/Navbar/Settings/SettingsContainer";


// ctrl + shift + N


//const DialogsContainer = React.lazy(() => import("./components/Navbar/Dialogs/DialogsContainer"))
//const UsersContainer = React.lazy(() => import("./components/Navbar/Users/UsersContainer"))
//const ProfileContainer = React.lazy(() => import("./components/Profile/ProfileContainer"))
const Music = React.lazy(() => import("./components/Navbar/Music/Music"))
//const SettingsContainer = React.lazy(() => import("./components/Navbar/Settings/SettingsContainer"))
const News = React.lazy(() => import("./components/Navbar/News/News"))



type MapPropsType = ReturnType<typeof mapStateToProps>
type DispatchPropsType = {
    initializeApp: () => void
    getFriends: (currentPage: number, pageSize: number) => void
}

class App extends React.Component <DispatchPropsType & MapPropsType> {

    catchAllUnhandledErrors = (e: PromiseRejectionEvent) => {
        alert('Something wrong')
    }

    componentDidMount() {
        this.props.getFriends( 1,   5)
        this.props.initializeApp()
        window.addEventListener('unhandledrejection', this.catchAllUnhandledErrors)
    };

    render() {
        if (!this.props.initialized) {
            return <Prelouder/>
        }


        return (
            <>
                <div className='app-wrapper'>
                    <React.Suspense fallback={<Prelouder/>}>
                        <Router>
                            <HeaderMy />
                            <Nav/>
                            <div className="app-wrapper-content">
                                    <Routes>
                                        <Route path="/social-network-react" element={<Navigate to={'/profile'} /> } />
                                        <Route path='/dialogs/*'
                                               element={<DialogsContainer/>}/>
                                        <Route path='/profile'
                                               element={<ProfileContainer1/>}
                                        />
                                        <Route path='/profile/:userId'
                                               element={<ProfileContainer1/>}
                                        />
                                        <Route path='/login'
                                               element={<Login />}
                                        />
                                        <Route path='/news' element={<News/>}/>
                                        <Route path='/music' element={<Music/>}/>
                                        <Route path='/users' element={<UsersContainer />}/>
                                        <Route path='/settings' element={<SettingsContainer/>}/>
                                        <Route path='/chat' element={<ChatPage/>}/>
                                        <Route path='*' element={<div className='error'>404 NOT FOUND</div>}/>
                                    </Routes>
                            </div>
                        </Router>
                    </React.Suspense>
                </div>
            </>
        )
    }
}


const mapStateToProps = (state: AppStateType) => ({
    initialized: state.app.initialized
})

let AppContainer = compose<React.ComponentType>(connect(mapStateToProps,
    {initializeApp, getFriends}))(App)


const SamuraiJsApp: React.FC = () => {
    return <Provider store={store}>
        <AppContainer/>
    </Provider>
}

export default SamuraiJsApp


