import {InferActionsType} from "./redux-store";


type dialogType = {
    id: number,
    message: string
}
export type massageType = {
    id: number,
    name: string
}
export type initialStateType = typeof initialState
type ActionsType = InferActionsType<typeof actions>

let initialState = {
    messages: [
        {id: 1, message: 'hi'},
        {id: 2, message: 'How are you'},
        {id: 3, message: 'yo'},] as Array<dialogType>,
    dialogs: [
        {id: 1, name: 'Svetlana'},
        {id: 2, name: 'Sasha'},
        {id: 3, name: 'Oleg'},
        {id: 4, name: 'Sasha'},
        {id: 5, name: 'Tanya'},] as Array<massageType>
}

const dialogsReducer = (state = initialState, action: ActionsType): initialStateType => {
    switch (action.type) {
        case "ADD_MES": {
            let newMes = action.newMesText;
            return { ...state,
                messages: [ ...state.messages, { id: 4, message: newMes}]
            }
        }
        default:
            return state
    }
}

export const actions = {
    addMesKey: (newMesText: string) => ({ type: 'ADD_MES', newMesText } as const)
}



export default dialogsReducer;




