// @ts-ignore
import {instance, ResponseType, ResultCodeEnum, ResultCodeForCaptchaEnum} from "./api";

type MeResponseDataType = {
    id: number,
    email: string,
    login: string
}

type LoginResponseType = {
    userId: number
}


export const authAPI = {
    userLogin() {
        return instance.get<ResponseType<MeResponseDataType>>(`auth/me`).then(res => res.data)
    },
    login(email: string, password: string, rememberMe = false, captcha: null | string = '') {
        return instance.post<ResponseType<LoginResponseType, ResultCodeEnum | ResultCodeForCaptchaEnum>>(`auth/login`, {email, password, rememberMe, captcha})
            .then(res => res.data)
    },
    logout() {
        return instance.delete(`auth/login`)
    },
}