import {friendType, UserType} from "../types/types";
import {BaseThunkType, InferActionsType} from "./redux-store";
import {Dispatch} from "redux";
import {usersAPI} from "../api/usersAPI";
import {FormAction} from "redux-form";
import {ResponseType} from "../api/api";


let initialState = {
    users: [] as Array<UserType> ,
    pageSize: 21 as number,
    totalUsersCount: 0 as number,
    currentPage: 1 as number,
    isFetching: false as boolean | null,
    followingInProgress: [] as Array<number>, // array users id
    friendsToNavBar: [] as Array<friendType>,
    filter: {
        term: '',
        friend: null as null | boolean
    }
}

type InitialState = typeof initialState
export type FilterType = typeof initialState.filter
type ActionsType = InferActionsType<typeof actions>

const profileReducer = (state = initialState, action: ActionsType): InitialState => {
    switch (action.type){
        case "FOLLOW":
            return  {
                ...state,
                users: state.users.map( u => {
                    if(u.id === action.userId){
                        return {...u, followed: true}
                    }
                    return u
                } )
            }
        case "UNFOLLOW":
            return {
                ...state,
                users: state.users.map( u => {
                    if(u.id === action.userId){
                        return {...u, followed: false}
                    }
                    return u
                } )
            }
        case "SET_USERS":
            return { ...state, users: action.users }
        case "SET_CURRENT_PAGE":
            return { ...state, currentPage: action.currentPage }
        case "SET_TOTAL_USERS_COUNT":
            return { ...state, totalUsersCount: action.count }
        case "TOGGLE_IS_FETCHING":
            return { ...state, isFetching: action.isFetching }
        case "TOGGLE_IS_FOLLOWING_PROGRESS":
            return { ...state,
                followingInProgress: action.isFetching
                ? [ ...state.followingInProgress, action.userId ]
                : state.followingInProgress.filter(id => id != action.userId)
            }
        case "SET_FRIENDS":
            return { ...state, friendsToNavBar: action.friendsToNavBar }
        case "SET_FILTER":
            return {...state, filter: action.filter}
        default:
            return state
    }

}


export const actions = {
    followSuccess: (userId: number) => ({ type: 'FOLLOW', userId } as const),
    unfollowSuccess: (userId: number) => ({ type: 'UNFOLLOW', userId } as const),
    setUsers: (users: Array<UserType>) => ({ type: 'SET_USERS', users } as const),
    setCurrentPage: (currentPage: number) => ({ type: 'SET_CURRENT_PAGE', currentPage } as const),
    setTotalUsersCount: (totalUsersCount: number) => ({ type: 'SET_TOTAL_USERS_COUNT', count: totalUsersCount } as const),
    toggleIsFetching: (isFetching: boolean) => ({ type: 'TOGGLE_IS_FETCHING', isFetching } as const),
    toggleFollowingProgress: (isFetching: boolean, userId: number) => ({ type: 'TOGGLE_IS_FOLLOWING_PROGRESS', isFetching, userId } as const),
    setFriends: (friendsToNavBar: Array<friendType>) => ( {type: 'SET_FRIENDS', friendsToNavBar: friendsToNavBar} as const),
    setFilter: (filter: FilterType) => ( {type: 'SET_FILTER', filter} as const)
}


type ThunkType = BaseThunkType<ActionsType | FormAction>


export const getFriends = (currentPage: number, pageSize: number): ThunkType =>
    async (dispatch) => {
    let data = await usersAPI.userFollowFriend(currentPage = 1, pageSize = 5)
        dispatch(actions.setFriends(data.items))
}

export const requestUsers = (currentPage: number, pageSize: number, filter: FilterType): ThunkType =>
    async (dispatch ) => {
    dispatch(actions.toggleIsFetching(true))
    dispatch(actions.setFilter(filter))
    dispatch(actions.setCurrentPage(currentPage))

    let data = await usersAPI.getUsers(currentPage, pageSize, filter.term, filter.friend)
    dispatch(actions.toggleIsFetching(false))
    dispatch(actions.setUsers(data.items))
    dispatch(actions.setTotalUsersCount(data.totalCount))
}

const _unfollowFollow = async (userId: number, dispatch: Dispatch<ActionsType>,
                               apiMethod: (userId: number) => Promise<ResponseType>, actionCreator:
    (userId: number) => ActionsType) => {  // - внутрішня функція
    dispatch(actions.toggleFollowingProgress(true, userId))
    let response = await apiMethod(userId)
    initialState.friendsToNavBar = []
    let data = await usersAPI.userFollowFriend( 1,  5)
    dispatch(actions.setFriends(data.items))
    if (response.resultCode == 0) {
        dispatch(actionCreator(userId))
    }
    dispatch(actions.toggleFollowingProgress(false, userId))
}

export const unfollow = (userId: number): ThunkType => async (dispatch) => {
    await _unfollowFollow( userId, dispatch, usersAPI.userUnfollow.bind(usersAPI), actions.unfollowSuccess)
}

export const follow = (userId: number): ThunkType => async (dispatch) => {
    await _unfollowFollow( userId, dispatch, usersAPI.userFollow.bind(usersAPI), actions.followSuccess)
}

export default profileReducer;



