import React, {useEffect} from "react";
import s from './Users.module.css'
import Paginator from "../../common/Paginator/Paginator";
import User from "./User";
import {FormSearch} from "./FormSearch";
import {actions, FilterType, follow, requestUsers, unfollow} from "../../../redax/users-reducer";
import {useDispatch, useSelector} from "react-redux";
import {
    getCurrentPage, getFilter, getFollowingInProgress,
    getPageSize, getTotalUsersCount, getUsers
} from "../../../redax/users-selectors";
import {useLocation, useNavigate} from "react-router-dom";
import queryString from "query-string"

type PropsType = {
    //follow: (userId: number) => void
    //unfollow: (userId: number) => void
}

type queryType = { term?: string, page?: string, friend?: string };

export const Users: React.FC<PropsType> = (props) => {

    const users = useSelector(getUsers)
    const currentPage = useSelector(getCurrentPage)
    const totalUsersCount = useSelector(getTotalUsersCount)
    const pageSize = useSelector(getPageSize)
    const followingInProgress = useSelector(getFollowingInProgress)
    const filter = useSelector(getFilter)

    const dispatch = useDispatch()

    const navigate = useNavigate()
    const location = useLocation()


    useEffect(() => {
        const parsed = queryString.parse(location.search) as queryType

        let actualPage = currentPage
        let actualFilter = filter

        if(!!parsed.page) actualPage = Number(parsed.page)

        if(!!parsed.term) actualFilter = {...actualFilter, term: parsed.term as string}
        switch (parsed.friend){
            case 'null':
                actualFilter = {...actualFilter, friend: null}
                break
            case 'true':
                actualFilter = {...actualFilter, friend: true}
                break
            case 'false':
                actualFilter = {...actualFilter, friend: false}
                break
        }

        //if(!!parsed.friend) actualFilter = {...actualFilter, friend: parsed.friend ===  'null' ? null : parsed.friend === 'true' ? true : 'false'}

        dispatch(requestUsers(actualPage, pageSize, actualFilter))
    }, [])


    useEffect(() => {
         let query: queryType = {}

        if(!!filter.term) query.term = filter.term
        if(filter.friend !== null) query.friend = String(filter.friend)
        if(currentPage !== 1) query.page = String(currentPage)

       navigate(`/users?` + queryString.stringify(query))
        //navigate('/users' + `?term=${filter.term}&friend=${filter.friend}&page=${currentPage}`)
    }, [filter, currentPage])


    const onPageChanged = (pageNumber: number) => {
        dispatch(actions.setCurrentPage(pageNumber));
        dispatch(requestUsers(pageNumber, pageSize, filter))
    }

    const onFilterChanged = (filter: FilterType) => {
        dispatch(requestUsers(1, pageSize, filter))
    }

    const followBut = (userId: number) => {
        dispatch(follow(userId))
    }

    const unfollowBut = (userId: number) => {
        dispatch(unfollow(userId))
    }

    return <>
        <div className={s.box}>
            <div>
                <FormSearch onFilterChanged={onFilterChanged}/>
            </div>

            <div className={s.boxPaginator}>
                <Paginator currentPage={currentPage} onPageChanged={onPageChanged}
                           totalUsersCount={totalUsersCount} pageSize={pageSize}/>
            </div>
            {
                users.map(u => <User key={u.id} user={u} followingInProgress={followingInProgress}
                                     unfollow={unfollowBut} follow={followBut}
                />)
            }
        </div>
    </>
}



