import React, {Key, SyntheticEvent} from "react";
import s from "./Note.module.css"
import {noteType} from "../../../../redax/setting-reducer";

type PropsType = {
    message: string
    deleteNote: (e: SyntheticEvent<HTMLDivElement>) => void
}

const Note: React.FC<PropsType> = (props) => {
    return (
        <div onClick={props.deleteNote} className={s.note}>{props.message}</div>
    )
}

export default Note