import React from 'react';
import s from './Header.module.css'
import {NavLink} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {AppStateType} from "../../redax/redux-store";
import {logout} from "../../redax/auth-reducer";
import {Avatar, Button, Col, Row} from "antd";
import {UserOutlined} from "@ant-design/icons";
import {Header} from "antd/lib/layout/layout";


export const HeaderMy: React.FC = () => {
    // if(!props.isAuth){<Prelouder />}   || !props.isAuth ? <Prelouder /> : ''

    const isAuth = useSelector((state: AppStateType) => state.auth.isAuth)
    const login = useSelector((state: AppStateType) => state.auth.login)

    const dispatch = useDispatch()

    const logoutBut = () => {
        dispatch(logout())
    }

    return (

        <Header className="site-layout-background" style={{padding: 0}}>
            <Row>
                <Col span={17}>
                    <div className={s.header}>
                        <img src='https://upload.wikimedia.org/wikipedia/commons/thumb/e/ea/Superman_shield.svg/1280px-Superman_shield.svg.png'/>
                    </div>
                    </Col>
                <Col span={7}>
                    <div className={s.loginBlock}>
                        {
                            isAuth
                                ? <div>
                                    <Avatar style={{backgroundColor: '#87d068'}} icon={<UserOutlined/>}/><span>___</span>
                                    {login}  <Button onClick={logoutBut}>Log Out</Button></div>
                                : <NavLink to={'/login'}>Login</NavLink>
                        }

                    </div>
                </Col>
            </Row>
        </Header>
    )
}