import React, {SyntheticEvent, useEffect, useState, FC, useLayoutEffect, useRef, memo, useMemo} from "react";
import s from "./Settings.module.css";
import Note from "./Note/Note";
import {noteType} from "../../../redax/setting-reducer";
import {ChangeEvent} from "react";

type PropsType = {
    changeColor: (value: string) => void
    updateNote: (text: string) => void
    note: Array<noteType>
    addNote: () => void
    status: string
}

export const Settings: React.FC<PropsType> = (props) => {

    const [state, setState] = useState<boolean>(true);
    const [but1, setBut1] = useState(() => 10)
    const [but2, setBut2] = useState(() => 10)
    const [but, setBut] = useState({c1: 10, c2: 10})

    let changeColors = (e: ChangeEvent<HTMLSelectElement>) => {
        props.changeColor(e.target.value)
    }

    let addNote = () => {
        props.addNote()
    }

    let onChangeText = (e: ChangeEvent<HTMLTextAreaElement>) => {
        let text = e.target.value
        props.updateNote(text)
    }


    let deleteNote = (e: SyntheticEvent<HTMLDivElement>) => {
        let node = e.currentTarget.innerText
        for (let i = 0; i < props.note.length; i++) {
            if (props.note[i].message === node) {
                props.note.splice(i, 1)
            }
        }
    }


    let noteElements = props.note.map(m => m.message.length < 1 ? '' :
        <Note deleteNote={deleteNote} message={m.message} key={m.id}/>)




    return (
        <div className={s.content}>
            <h1>Settings</h1>
            <h3>Background color</h3>
            <div>
                <select onChange={changeColors} name='colors' id='colors' className={s.colors}>
                    <option value="lightpink">lightpink</option>
                    <option value="violet">violet</option>
                    <option value="green">green</option>
                    <option value="yellow">yellow</option>
                </select>
            </div>

            <div className={s.areaBox}>
                <textarea className={s.area} onChange={onChangeText} value={props.status}></textarea>
            </div>
            <div>
                <button onClick={addNote}>ADD NOTE</button>
            </div>
            <div>
                {noteElements}
            </div>
            <p></p>
            <div>
                <button onClick={() => setState(prevState => !prevState)}>
                    {state.toString() === 'true' ? 'You cool!' : 'you nice!'}</button>
            </div>

            <div>
                <div>
                    {but.c1}
                    <div>
                        {/*<button onClick={() => setBut1((actual) => actual +1)}>plus</button>*/}
                        {/*<button onClick={ () => {setBut((actual) => {*/}
                        {/*    actual.c1++*/}
                        {/*    return actual*/}
                        {/*}) */}
                        {/*}}>plus</button></div>*/}
                        <button onClick={() => {
                            setBut((actual) => {
                                return {...actual, c1: actual.c1 + 1}
                            })
                        }}>plus
                        </button>
                    </div>
                </div>
                <div>
                    <div>
                        {but.c2}
                        <div>
                            <button onClick={() => {
                                setBut((actual) => {
                                    return {...actual, c2: actual.c2 + 1}
                                })
                            }}>plus</button>
                        </div>
                    </div>
                </div>
                <div>
                    <button onClick={() => {
                        setBut((actual) => {
                            return {...actual, c1: actual.c1 - 1, c2: actual.c2 - 1}
                        })
                    }}>minus
                    </button>
                </div>
            </div>

        </div>
    )
}


