import React from 'react';
import Profile from "./Profile";
import {useDispatch, useSelector} from "react-redux";
import {useNavigate, useParams} from "react-router-dom";
import {profileType} from "../../types/types";
import {AppStateType} from "../../redax/redux-store";
import {useEffect} from "react";
import {getStatus, getUserProfileHeader} from "../../redax/profile-reducer";


export const ProfileContainer1 = () => {

    let navigate = useNavigate();
    let params = useParams();
    const dispatch = useDispatch()

    const authorizedUserId = useSelector((state: AppStateType) => state.auth.userId)
    const isAuth = useSelector((state: AppStateType) => state.auth.isAuth)
    const profile = useSelector((state: AppStateType) => state.profilePage.profile)
    let userId = params.userId === undefined ? 23954 : Number(params.userId) as number | null

    useEffect(() => {
        let userId = params.userId === undefined ? 23954 : Number(params.userId) as number | null
        if(userId === undefined) {
            userId = authorizedUserId
        }
        if (!isAuth) {
            navigate('/login')
        }

        dispatch(getUserProfileHeader(userId))
        dispatch(getStatus(userId))

    }, [isAuth, userId])

    return <Profile profile={profile as profileType} userId={userId} />
}



