import {ResultCodeEnum, ResultCodeForCaptchaEnum, securityAPI} from "../api/api";
import {FormAction, stopSubmit} from "redux-form";
import {InferActionsType, BaseThunkType} from "./redux-store";
import {authAPI} from "../api/authAPI";



let initialState = {
    userId: 23954 as number | null,
    email: null as string | null,
    login: null as string | null,
    isFetching: false as boolean | null,
    isAuth: false as boolean,
    captchaUrl: null as string | null
};

export type initialStateType = typeof initialState

type ActionsType = InferActionsType<typeof actions>

const authReducer = (state = initialState, action: ActionsType): initialStateType => {
    switch (action.type){
        case "SET_USER_DATA":
            return {...state, ...action.data}
        case "TOGGLE_IS_FETCHING":
            return { ...state, isFetching: action.isFetching }
        case "GET_CAPTCHA_URL_SUCCESS":
            return { ...state, ...action.payload }
        default:
            return state
    }

}

export const actions = {
    setUserdata: (userId: number | null, email: string | null, login: string | null, isAuth: boolean) =>
        ({type: 'SET_USER_DATA', data: { userId, email, login, isAuth } } as const),
    getCaptchaUrlSuccess: (captchaUrl: string) => ({type: 'GET_CAPTCHA_URL_SUCCESS', payload: {captchaUrl} } as const),
    toggleIsFetching: (isFetching: boolean) => ({ type: 'TOGGLE_IS_FETCHING', isFetching } as const)

}

type ThunkType = BaseThunkType<ActionsType | FormAction>

export const loginOn = (): ThunkType =>
    async (dispatch) => {
    dispatch(actions.toggleIsFetching(true))
    let meData = await authAPI.userLogin()
    if (meData.resultCode === ResultCodeEnum.Success) {
        let {id, email, login} = meData.data
        dispatch(actions.setUserdata(id, email, login, true))
        dispatch(actions.toggleIsFetching(false))
    }
}

export const getCaptchaUrl = (): ThunkType =>
    async (dispatch) => {
    const response = await securityAPI.getCaptchaUrl()
    const captchaUrl = response.url
    dispatch(actions.getCaptchaUrlSuccess(captchaUrl))

}

export const login = (email: string, password: string, rememberMe: boolean, captcha: string): ThunkType =>
    async (dispatch) => {
        let data = await authAPI.login(email, password, rememberMe, captcha)
        if (data.resultCode === ResultCodeEnum.Success) {
            dispatch(loginOn())
        } else {
            if(data.resultCode === ResultCodeForCaptchaEnum.CaptchaIsRequired){
                dispatch(getCaptchaUrl())
            }
            let message = data.messages.length > 0 ? data.messages[0] : 'Some error'
            dispatch(stopSubmit('login', {_error: message}))
        }
}


export const logout = (): ThunkType =>
    async (dispatch) => {
    let response = await authAPI.logout()
    if (response.data.resultCode === 0) {
        dispatch(actions.setUserdata(null, null, null, false))
    }
}

export default authReducer


