import {GetItemsType, instance, ResponseType} from "./api";

export const usersAPI = {
    getUsers(currentPage: number = 1, pageSize: number, term: string = '', friend: null | boolean = null) {
        return instance.get<GetItemsType>(`users?page=${currentPage}&count=${pageSize}&term=${term}` + (friend === null ? '' : `&friend=${friend}`))
            .then(response => response.data)
    },
    userUnfollow(userId: number) {
        return instance.delete(`follow/` + userId)
            .then(response => response.data) as Promise<ResponseType>
    },
    userFollow(userId: number) {
        return instance.post<ResponseType>(`follow/` + userId, {})
            .then(response => response.data)
    },
    userFollowFriend(currentPage: number, pageSize: number) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}&friend=${true}`)
            .then(res => res.data)
    },
}