import React, {useEffect, useRef, useState} from "react";
import s from './ChatPage.module.css'
import {useNavigate} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {AppStateType} from "../../../redax/redux-store";
import {sendMessage, startMessagesListening, stopMessagesListening} from "../../../redax/chat-reducer";


const ChatPage: React.FC = () => {

    let navigate = useNavigate();
    const isAuth = useSelector((state: AppStateType) => state.auth.isAuth)

    useEffect(() => {
        if (!isAuth) {
            navigate('/login')
        }
    }, [isAuth])

    return <div>
        <Chat/>
    </div>

}

const Chat: React.FC = () => {

    const status = useSelector((state: AppStateType) => state.chat.status)

    const dispatch = useDispatch()

    useEffect(() => {
        //debugger
        dispatch(startMessagesListening())
        return () => {
            stopMessagesListening()
        }
    }, [])

    return <div>
        {status === 'error' && <div>Some error occurred. Please error page</div>}
        <>
            <div>
                <Messages/>
            </div>
            <div>
                <Textarea/>
            </div>
        </>
    </div>
}

const Messages: React.FC = () => {

    const messages = useSelector((state: AppStateType) => state.chat.messages)
    const mesFef = useRef<HTMLDivElement>(null)
    const [isAutoscroll, setIsAutoscroll] = useState(true)

    const scrollHandler = (e: React.UIEvent) => {
        const element = e.currentTarget
        if (Math.abs((element.scrollHeight - element.scrollTop) - element.clientHeight) < 310) {
            !isAutoscroll && setIsAutoscroll(true)
        } else {
            isAutoscroll && setIsAutoscroll(false)
        }
    }

    useEffect(() => {
       if (isAutoscroll) {
           setTimeout(() => {
               mesFef.current?.scrollIntoView({behavior: 'smooth'})
           }, 100)
        }
    }, [messages])


    return <div style={{height: '700px', overflowY: 'auto'}} onScroll={scrollHandler}>
        {
            messages.map((m, index) =>
                <Message userId={m.userId} photo={m.photo} message={m.message}
                         userName={m.userName} key={index}/>
            )
        }
        <div ref={mesFef}></div>
    </div>
}

const Message: React.FC<messageType> = React.memo( (props) => {

    return <div className={s.message}>
        <img src={props.photo} style={{width: '50px', borderRadius: '50%'}}/> <b>{props.userName}</b>
        <p>ID user: {props.userId}</p>
        <p>{props.message}</p>
    </div>
})

const Textarea: React.FC = () => {

    const [message, setMessage] = useState('')
    const status = useSelector((state: AppStateType) => state.chat.status)
    const dispatch = useDispatch()

    const sendMessageTA = () => {
        if (!message) {
            return
        }
        dispatch(sendMessage(message))
        setMessage('')
    }

    return <div>
        <div>
            <textarea onChange={(e) => setMessage(e.currentTarget.value)}
                      value={message}/>
        </div>
        <div>
            <button disabled={status !== 'ready'} onClick={sendMessageTA}>Send</button>
        </div>
    </div>
}

type messageType = {
    userId: number
    message: string,
    photo: string
    userName: string

}


export default ChatPage