export type FieldValidatorType = (value: string) => string | undefined

export const required: FieldValidatorType = (value) => {
    if(value) return undefined
    return "Field"
}

export const maxSymbol20 = (maxLength: number): FieldValidatorType => (values) => {
    if(values.length > maxLength) return `Symbol max ${maxLength}`
    return undefined
}

