import React from 'react'
import {ChangeEvent, useState} from 'react';
import s from './ProfileInfo.module.css'
import Prelouder from "../../common/Prelouder";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import {ProfileReduxForm} from "./ProfileDataForm";
import {profileType} from "../../../types/types";
import {useDispatch, useSelector} from "react-redux";
import {AppStateType} from "../../../redax/redux-store";
import {savePhoto, saveProfile, updateStatus} from "../../../redax/profile-reducer";


export type PropsType = {
    profile: profileType
    userId: number | null
}

const ProfileInfo: React.FC<PropsType> = ({profile, userId}) => {

    let isOwner = userId === 23954 ? true : false
    const status = useSelector((state: AppStateType) => state.profilePage.status)

    const dispatch = useDispatch()

    let [editeMode, setEditeMode] = useState(false)

    if (!profile.userId) {
        return <Prelouder/>
    }

    const onMainPhotoSelected = (e: ChangeEvent<HTMLInputElement>) => {
        if (e.target.files?.length) {
            dispatch(savePhoto(e.target.files[0]))
        }
    }

    const onSubmit = (formData: profileType) => {
        dispatch(saveProfile(formData))
            //.then(() => {setEditeMode(false)}))
        setTimeout(()=>{
            setEditeMode(false)
        }, 1000)
    }

    const updateStatusBut = (status: string) => {
        dispatch(updateStatus(status))
    }

    return (
        <div>
            <div className={s.content}>
                <img
                    src='https://img1.akspic.ru/previews/6/6/1/7/6/167166/167166-cifrovoe_iskusstvo_nochnoj_gorod-cifrovoe_iskusstvo-art-artist-proizvedenie_iskusstva-x750.jpg'/>
            </div>

            <div className={s.profileInfo}>
                <img src={profile.photos.small != null ? profile.photos.small
                    : 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSAaeVfXxyG1sNBohvr-x5NOCzM9lvcF_pTzA&usqp=CAU'}
                     className={s.fotoProfile}/><p></p>
                {isOwner && <input type='file' onChange={onMainPhotoSelected}/>}
                {editeMode
                    ? <ProfileReduxForm onSubmit={onSubmit} initialValues={profile}/>
                    : <ProfileData profile={profile} isOwner={isOwner} goToEditeMode={() => {
                        setEditeMode(true)
                    }}/>}
                <ProfileStatusWithHooks updateStatus={updateStatusBut} status={status}/>
            </div>
        </div>

    )
}


type ProfileData = {
    isOwner: boolean
    goToEditeMode: () => void
    profile: profileType
}

const ProfileData: React.FC<ProfileData> = (props) => {
    return (
        <div>
            {props.isOwner && <div>
                <button onClick={props.goToEditeMode}>Edite</button>
            </div>}
            <p>{props.profile.fullName}</p>
            <p>User ID = {props.profile.userId}</p>
            <p>Looking for a job: {props.profile.lookingForAJob ? 'yes' : 'no'}</p>
            {props.profile.lookingForAJob &&
                <p>My professional skills: {props.profile.lookingForAJobDescription}</p>
            }
            <div>
                <b>Contacts</b>: {Object
                .keys(props.profile.contacts)
                .map((key) =>
                <Contacts key={key} contactsTitle={key} contactsValue={props.profile.contacts[key as keyof contactsTypeKey]}/>
            )}
            </div>
        </div>
    )
}

type contactsTypeKey = {
    facebook: string,
    website: string,
    vk: string,
    twitter: string,
    instagram: string,
    youtube: string,
    github: string,
    mainLink: string,
}
type ContactsType = {
    contactsTitle: string
    contactsValue: string
}

const Contacts: React.FC<ContactsType> = ({contactsTitle, contactsValue}) => {
    return <div className={s.contacts}><b>{contactsTitle}</b>: <a target={'_blank'} href={'https://'+contactsValue}>{contactsValue}</a></div>
}

export default ProfileInfo;



